import { Translate } from "next-translate";
import { mergeDeepRight } from "ramda";

import { PageContentProps } from "content/types";

import defaultContent from "../newMainPage/spain";

const content = (t: Translate, { version }: PageContentProps) =>
    mergeDeepRight(defaultContent(t, { version }), {
        isMailPage: true,
    });

export default content;
