import { Translate } from "next-translate";
import { mergeDeepRight } from "ramda";

import prices from "common/configs/prices.json";
import { priceFormatter } from "common/utils/priceFormatter";

import { PageContentProps } from "content/types";

import defaultContent from "./../remarketingPage/spain";

const pageContent = (t: Translate, { version, language }: PageContentProps) => {
    const discount = priceFormatter(
        prices.discount.c3[version],
        prices.currency[version],
        version
    );

    const content = defaultContent(t, { version, language });

    return mergeDeepRight(content, {
        head: {
            title: t("remarketing:title", { discount }),
            subtitle: t("remarketing:subtitle", { discount }),
            priceLabel: {
                desktop: {
                    png: `/special/images/remarketing/price-label-desktop-300-${version}.png`,
                },
                mobile: {
                    png: `/special/images/remarketing/price-label-mobile-300-${version}.png`,
                },
            },
        },
        advantagesGrid: {
            list: [
                content.advantagesGrid.list[0],
                content.advantagesGrid.list[1],
                mergeDeepRight(content.advantagesGrid.list[2], {
                    price: {
                        monthly: {
                            amount: prices.regularPrice.monthly.c3[version],
                        },
                        once: {
                            amount: prices.regularPrice.once.c3[version],
                        },
                    },
                }),
            ],
        },
    });
};

export default pageContent;
