import { Translate } from "next-translate";
import { mergeDeepRight } from "ramda";

import prices from "common/configs/prices.json";

import { PageContentProps } from "content/types";

import defaultContent from "./spain";

const pageContent = (t: Translate, { version }: PageContentProps) =>
    mergeDeepRight(defaultContent(t, { version }), {
        price: {
            pricesList: [
                {
                    name: t("prices:Pay monthly"),
                    paymentMonthly: {
                        payment: {
                            oldPrice: null,
                            amount:
                                prices.regularPrice.monthly.baseWD[version] ||
                                prices.regularPrice.monthly.base[version],
                        },
                        month: `${prices.currency[version]}/${t("month")}`,
                        period: `${prices.quantityOfInstalments[version]} ${t(
                            `prices:instalments`
                        )}`,
                        currency: prices.currency[version],
                    },
                },
                {
                    name: t("prices:Pay at once"),
                    paymentOnce: {
                        payment: {
                            oldPrice: prices.regularPrice.once.base[version],
                            amount:
                                prices.regularPrice.once.baseWD[version] ||
                                prices.regularPrice.once.base[version],
                        },
                        currency: prices.currency[version],
                    },
                },
            ],
        },
    });

export default pageContent;
