import { Translate } from "next-translate";
import { mergeDeepRight } from "ramda";

import prices from "common/configs/prices.json";

import { PageContentProps } from "content/types";

import defaultContent from "./spain";

const pageContent = (t: Translate, { version, language }: PageContentProps) =>
    mergeDeepRight(defaultContent(t, { version, language }), {
        advantages: {
            first: {
                list: [
                    t("advantages-block:firstListItem1"),
                    t("advantages-block:firstListItem2"),
                    t("advantages-block:firstListItem3"),
                    t("advantages-block:firstListItem4"),
                ],
            },
        },
        price: {
            pricesList: [
                {
                    name: t("prices:Pay monthly"),
                    paymentMonthly: {
                        payment: {
                            oldPrice: null,
                            amount: prices.regularPrice.monthly.base[version],
                        },
                        month: `${prices.currency[version]}/${t("month")}`,
                        period: `${prices.quantityOfInstalments[version]} ${t(
                            `prices:instalments`
                        )}`,
                    },
                    currency: prices.currency[version],
                },
                {
                    name: t("prices:Pay at once"),
                    paymentOnce: {
                        payment: {
                            oldPrice: null,
                            amount: prices.regularPrice.once.base[version],
                        },
                    },
                    currency: prices.currency[version],
                },
            ],
        },
    });

export default pageContent;
