import { Translate } from "next-translate";
import { mergeDeepRight } from "ramda";

import { PageContentProps } from "content/types";

import defaultContent from "./spain";

export default (t: Translate, { version }: PageContentProps) =>
    mergeDeepRight(defaultContent(t, { version }), {
        image: {
            mobile: {
                jpeg: "/special/images/new/referral/referral-pt-mobile.jpg",
                webp: "/special/images/new/referral/referral-pt-mobile.webp",
            },
            desktop: {
                jpeg: "/special/images/new/referral/referral-pt-desktop.jpeg",
                webp: "/special/images/new/referral/referral-pt-desktop.webp",
            },
        },
    });
